import { useNavigate } from 'react-router-dom';
import cssStyle from '../css/Modal.module.css';
export default function Modal({ setModal }) {
  let navigate = useNavigate();
  return (
    <div className={cssStyle.modal}>
      <div className={cssStyle.inner}>
        <p> .</p>
        <button
          onClick={() => {
            setModal(false);
          }}
        >
           
        </button>
        <button
          onClick={() => {
            navigate('/cart');
          }}
        >
           
        </button>
      </div>
    </div>
  );
}
