import { createSlice } from '@reduxjs/toolkit';

export let user = createSlice({
  name: 'user',
  initialState: { id: 'somypark', name: '', rating: 2 },
  reducers: {
    changeName(prev) {
      prev.name = ' VVIP ';
    },
    changeRate(prev, action) {
      prev.rating += action.payload;
    },
  },
});

export let { changeName, changeRate } = user.actions;
